<template>
	<div>
		<div class="header-abs" v-show="absShow">
			<div onclick="javascript:history.go(-1)" class="back">
				<span class="css_l_arrow"></span>
			</div>	
			<router-link to="/Cart" class="toCart" tag="span"></router-link>
		</div>
		
		<div class="header-fix" v-show="!absShow" :style="opacityStyle">
			<router-link to="/" tag="span">返回</router-link>
			<router-link to="/Cart" tag="span">购物车</router-link>	
		</div>
	</div>
</template>
<script>
	export default{
		data (){
			return {
				absShow:true,
				opacityStyle:{
					opacity:0
				}
			}
		},
		methods :{
			handlerScroll (){
				var st=window.pageYOffset;
				if(st>60){
					let opacity=st/110;
					opacity=Math.min(opacity,1)
					this.opacityStyle={
						opacity
					}
					this.absShow=false;
				}else{
					this.absShow=true;
				}
			}
		},
		activated (){
			window.addEventListener('scroll', this.handlerScroll);
		},
		deactivated (){
			window.removeEventListener("scroll",this.handlerScroll)
		}
	}
</script>
<style scoped>
	.header-abs{
		display: flex;
		justify-content: space-between;
		position: absolute;
		width:100%;
		z-index: 99;
		padding: 5px 3% 0px;
		box-sizing: border-box;
	}
	.back,.toCart{
		width: 40px;
		height: 40px;
		border-radius: 100%;
		position: relative;
		display: block;
/*		position: absolute;
		left: 10px;
		top: 10px;*/
		background: rgba(0,0,0,0.4);
		/*z-index: 98;*/
	}
	.toCart{
		background:rgba(0,0,0,0.4) url(../assets/img/line-cart.svg) center center no-repeat;
		-webkit-background-size: 30px;
		background-size: 30px;
	}
/*	{
     position:absolute;
     width: 40px;
     height: 40px;
     background:rgba(0,0,0,0.4);
     border-radius: 50%;
     top: 20px;
     right: 20px;
     z-index: 98;
}*/
	.header-fix{
		height: 45px;
		line-height: 45px;
		background: #e6292c;
		color: #fff;
		position: fixed;
		top:0px;
		left:0px;
		right: 0px;
		z-index: 98;
		display: flex;
		padding:0px  20px ;
		justify-content: space-between;
	}
	.css_l_arrow {
	    width: 0.1rem;
	    height: 0.1rem;
	    border-top: 0.02rem solid #fff;
	    border-left: 0.02rem solid #fff;
	    -webkit-transform: rotate(-45deg);
	    transform: rotate(-45deg);
	    position: absolute;
	    left: 50%;
	    top: 50%;
	    margin-top: -0.05rem;
	    margin-left: -0.05rem;
	}
</style>


