<template>
	<div @click="handlerClose" v-if="imgs.length">
		<swiper :options="swiperOption" ref="mySwiper">
		  <swiper-slide v-for="(item,index) of imgs" :key="index">
				<img :src="item">
		  </swiper-slide>
		</swiper>
		 <div :class="['swiper-pagination','swiper-pagination'+type]"   slot="pagination"></div>
	</div>
</template>
<script>
	export default {
		props:{
			imgs:{
				type:Array,
				default (){
					return [require("../assets/img/timg.jpg")]
				}
			},
			type:Number
		},
		data (){
			return {
				swiperOption:{
					paginationType:"fraction",
					pagination:".swiper-pagination"+this.type,
					observer:true,
					observeParents:true
				}
			}
		},
	    computed: {
	      swiper() {
	        return this.$refs.mySwiper.swiper
	      }
	    },
	    mounted() {
	      // current swiper instance
	      // 然后你就可以使用当前上下文内的swiper对象去做你想做的事了
	      // this.swiper.slideTo(3, 1000, false)
	    },
		methods:{
			handlerClose (){
				this.$emit("close")
			}
		}
	}
</script>
<style scoped>

</style>


